import React, {Component} from "react";
import $ from "jquery";
import "./Player.css";
// NOTE: This ajax call to the Spotify API returns:
// ALBUM(object), Playing(Bool), Progress (Int) 
class Player extends Component {

  state = {
    item: null,
    is_playing: null,
    progress_ms: null
  }

  componentDidMount(){
    this.getPlayerData();
    this.getCurrentPlaying();
  }

  getPlayerData(){
    $.ajax({
      url: "https://api.spotify.com/v1/me/player",
      type: "GET",
      beforeSend: (xhr) => {
        xhr.setRequestHeader("Authorization", "Bearer " + this.props.token);
      },
      success: (data) => {
        if(!data){
          return;
        }
        this.setState({
          item: data.item,
          is_playing: data.is_playing,
          progress_ms: data.progress_ms,
        });

      }
    });
  }

  getCurrentPlaying(){
    console.log(this.state.item);
    console.log(this.state.is_playing);
    console.log(this.state.progress_ms);
  }

  render(){
    return (
      <div className="App-player">
        <h1>Player</h1>
        {this.getCurrentPlaying()}
      </div>
    )
  }
}

export default Player;